<template>
	<div class="page2_4">
		<div class="page-top" style="margin-top: -4px;height: 0;margin-left: 20px;">
			<Place class="fl" :data="msg"/>
		</div>
		<div class="content_page" style="margin-top: 0">
			<div class="am-contain er">
				<div>
					<div class="head over">
		                <h3 class="fl"><span class="i"></span> 
		                	<span style="font-size: 18px;font-weight: normal">
		                		公司存在异常点：<span style="color: red;font-size: 22px;">{{getNum[0]}}个</span>
		                	</span>
		                </h3>
		            </div>
		            <div class="content box-border-shadow">
		            	<div class="tishi-word">温馨提示：点击下方所显示的异常点，可追溯到该异常点的线索</div>
						<div id="wordCloud" style="width: 100%;min-height:300px;margin: 0 auto">
							<img style="display:block;margin: 150px auto;" v-if="imgShow" src="../../assets/no-yc.png">
						</div>
		            </div>
				</div>
				<div>
					<div class="head over">
		                <h3 class="fl"><span class="i"></span> 
		                	<span style="font-size: 18px;font-weight: normal">
		                		公司正常指标：<span style="color: red;font-size: 22px;">{{getNum[1]}}个</span>
		                	</span>
		                </h3>
		            </div>
		            <div class="content box-border-shadow">
		            	<div class="tishi-word">温馨提示：点击下方所显示的指标，可追溯到该指标的数据</div>
						<div id="wordCloud2" style="width: 100%;min-height:300px;margin: 0 auto">
							<img style="display:block;margin: 150px auto;" v-if="imgShow2" src="../../assets/no-yc.png">
						</div>
		            </div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Place from './page/place'
	import * as echarts from 'echarts';
	require('echarts-wordcloud');
	
	export default{
		name:'page2',
		data(){
			return{
				msg:["审计疑点线索挖掘","发现疑点"],
				imgShow:false,
				imgShow2:false
			}
		},
		components:{
			Place
		},
		computed:{
			company(){
				return JSON.parse(this.$store.getters.getCompany);
			},
			getWord(){
				return this.$store.getters.getWord;
			},
			dataWordCloud(){
				if(JSON.parse(sessionStorage.getItem("WordCloud"))){
					let datas=JSON.parse(sessionStorage.getItem("WordCloud"));
					console.log(datas);
					let yc_datas=[];
					let zc_datas=[];
					datas.forEach((v,i)=>{
						let name=v.name;
						if(name.length>6){
							v.name=name.substr(0,5)+'...';
							v.nameSub=name;
						}else{
							v.name=name.substr(0,5);
							v.nameSub=name;
						}
						// console.log(Number(v.num[0]));
						if(Number(v.num[0])>=0.5||Number(v.num[1])>=0.5||Number(v.num[2])>=0.5){
							// console.log(v);
							yc_datas.push(v);
						}else{
							// console.log(v);
							zc_datas.push(v);
						}
					});
					console.log(yc_datas);
					console.log(zc_datas);
					return [yc_datas,zc_datas];
				}else{
					return [];
				}
				
			},
			getNum(){
				return [this.dataWordCloud[0].length,this.dataWordCloud[1].length,];
			}
		},
		methods:{
			optionWordCloud(data){
				return {
	                title:{
	                    text:"",
	                },
	                tooltip: {
	                	formatter: function (params) {
						    // console.log(params.data.num);
						    return '疑点指标：'+params.data.nameSub+
						    '<br/>总异常分：'+params.data.value+
						    '<br/>合规异常分：'+params.data.num[0]+
						 	'<br/>真实异常分：'+params.data.num[1]+ 
						 	'<br/>绩效异常分：'+params.data.num[2]
						}
	                },
	                series: [{
	                    type: 'wordCloud',
	                    sizeRange: [20, 30],
	                    rotationRange: [0, 45,-45],
	                    shape: 'circle',
	                    textStyle: {
	                        normal: {
	                            color: function() {
	                                return 'rgb(' + [
	                                    Math.round(Math.random() * 160),
	                                     Math.round(Math.random() * 160),
	                                    Math.round(Math.random() * 160)
	                                ].join(',') + ')';
	                            }
	                        },
	                        emphasis: {
	                            shadowBlur: 10,
	                            shadowColor: '#333'
	                        }
	                    },
	                    data: data
	                }]
	            };
			}
		},
		mounted(){
			var _this=this;
	        if(_this.dataWordCloud[0].length>0){
	        	var myWordCloud = echarts.init(document.getElementById('wordCloud'));
	        	myWordCloud.setOption(this.optionWordCloud(_this.dataWordCloud[0]));
				myWordCloud.on('click', function (params) {
					console.log(params);
					sessionStorage.setItem("word",params.data.nameSub);
					sessionStorage.setItem("radarData",params.data.num);
					_this.$store.commit('updateWord',params.data.nameSub);
					_this.$store.dispatch('updateWord1',[params.data.nameSub]);
					_this.$store.commit('updateFaguiNode',params.data.nameSub);
					_this.$store.commit('updateXinwenNode',params.data.nameSub);
					_this.$store.dispatch('updateLeftNav',[_this.getWord,_this.company.id]);
					_this.$store.dispatch('updateFagui',[_this.getWord+',',_this.company.id]);
					_this.$store.dispatch('updateXinwen',[_this.getWord+',',_this.company.id]);
	               _this.$router.push({ path: 'page2_1'});
	            });
	             $(window).resize(function(){
						myWordCloud.resize({
							width:'auto',
							height:'auto'
						})
				  });
	        }else{
	        	this.imgShow=true;
	        }
	        if(_this.dataWordCloud[1].length>0){
	        	var myWordCloud2 = echarts.init(document.getElementById('wordCloud2'));
	        	myWordCloud2.setOption(this.optionWordCloud(_this.dataWordCloud[1]));
				myWordCloud2.on('click', function (params) {
					console.log(params);
					sessionStorage.setItem("word",params.data.nameSub);
					sessionStorage.setItem("radarData",params.data.num);
					_this.$store.commit('updateWord',params.data.nameSub);
					_this.$store.dispatch('updateWord1',[params.data.nameSub]);
					_this.$store.commit('updateFaguiNode',params.data.nameSub);
					_this.$store.commit('updateXinwenNode',params.data.nameSub);
					_this.$store.dispatch('updateLeftNav',[_this.getWord,_this.company.id]);
					_this.$store.dispatch('updateFagui',[_this.getWord+',',_this.company.id]);
					_this.$store.dispatch('updateXinwen',[_this.getWord+',',_this.company.id]);
	               _this.$router.push({ path: 'page2_1'});
	            });
	             $(window).resize(function(){
						myWordCloud2.resize({
							width:'auto',
							height:'auto'
						})
				  });
	        }else{
	        	this.imgShow2=true;
	        }
   		}
	}
</script>
<style type="text/css" scoped="">
	.content_page{

	}
	.am-u-md-6{
		box-sizing: border-box;
	}
	.content_page .head{
		height: 30px;
    	line-height: 30px;
    	padding: 8px 6px;
    	box-sizing: content-box;
	}
	.content_page .head .i{
		display: inline-block;
	    width: 20px;
	    height: 20px;
	    vertical-align: middle;
	    margin-top: -5px;
	    background: url("../../assets/icon-yc.png") no-repeat center;
	}
	.content_page .content {
	    min-height: 300px;
        width: 100%;
        padding: 30px 60px;
        box-sizing: border-box;
        text-align: justify;
        border-radius: 8px;
        background-color: #fff;
	}

</style>


